<template>
  <div class="agents-index">
    <h1>Список сотрудников</h1>
    <router-link to="/agents/add" class="btn btn-primary">Добавить сотрудника</router-link>

    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>Фото</th>
          <th>Имя</th>
          <th>Фамилия</th>
          <th>Email</th>
          <th>Телефон</th>
          <th>Должность</th>
          <th>Годы опыта</th>
          <th>Дата регистрации</th>
          <th>Активен</th>
          <th>Действия</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="agent in agents" :key="agent.id">
          <td>{{ agent.id }}</td>
          <td>
            <img
              v-if="agent.photo_url"
              :src="getAssetImage(agent.photo_url)"
              :alt="agent.name"
              style="width: 40px; height: 40px; object-fit: cover; border-radius: 50%;"
            />
            <span v-else>—</span>
          </td>
          <td>{{ agent.name }}</td>
          <td>{{ agent.surname }}</td>
          <td>{{ agent.email }}</td>
          <td>{{ agent.phone || '—' }}</td>
          <td>{{ agent.position }}</td>
          <td>{{ agent.experience_years || 0 }}</td>
          <td>{{ formatDate(agent.created_at) }}</td>
          <td>
            <span :class="agent.is_available ? 'status-active' : 'status-inactive'">
              {{ agent.is_available ? 'Да' : 'Нет' }}
            </span>
          </td>
          <td>
            <router-link :to="{ name: 'AgentView', params: { id: agent.id } }" class="btn btn-info">
              Просмотр
            </router-link>
            <router-link :to="{ name: 'AgentEdit', params: { id: agent.id } }" class="btn btn-warning">
              Редактировать
            </router-link>
            <button @click="deleteAgent(agent.id)" class="btn btn-danger">Удалить</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Пагинация -->
    <div class="pagination">
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
        class="btn btn-secondary"
      >
        Предыдущая
      </button>
      <span>Страница {{ currentPage }} из {{ totalPages }}</span>
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="btn btn-secondary"
      >
        Следующая
      </button>
    </div>
  </div>
</template>

<script>
import { agentsApi } from '@/services/api';

export default {
  data() {
    return {
      agents: [],
      currentPage: 1,
      limit: 10,
      total: 0,
      totalPages: 0,
    };
  },
  created() {
    this.fetchAgents();
  },
  methods: {
    fetchAgents() {
      agentsApi.getAgents(this.currentPage, this.limit)
        .then(response => {
          if (!response.data || !response.data.meta) {
            throw new Error('Invalid response format from server');
          }
          this.agents = response.data.data;
          this.total = response.data.meta.total;
          this.totalPages = response.data.meta.totalPages;
        })
        .catch(error => {
          console.error('Ошибка при получении списка сотрудников:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    changePage(page) {
      if (page < 1 || page > this.totalPages) return;
      this.currentPage = page;
      this.fetchAgents();
    },
    deleteAgent(id) {
      if (!confirm('Вы уверены, что хотите удалить этого сотрудника?')) return;

      agentsApi.deleteAgent(id)
        .then(() => {
          this.agents = this.agents.filter(agent => agent.id !== id);
          alert('Сотрудник успешно удален!');
        })
        .catch(error => {
          console.error('Ошибка при удалении сотрудника:', error);
          alert(`Произошла ошибка при удалении сотрудника: ${error.message}`);
        });
    },
    formatDate(dateString) {
      if (!dateString) return 'Не указана';
      const date = new Date(dateString);
      return date.toLocaleDateString();
    },
    getAssetImage(imageName) {
      try {
        // Путь до папки assets
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/images/default-avatar.png'; // или '/assets/default.jpg' если дефолтное тоже в assets
      }
    },
  },
};
</script>

<style scoped>
.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}
.btn {
  margin-right: 5px;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 14px;
}
.pagination span {
  margin: 0 10px;
  font-size: 16px;
}

/* Стили для статуса */
.status-active {
  color: green;
  font-weight: bold;
}
.status-inactive {
  color: red;
  font-weight: bold;
}
</style>