<template>
  <div class="flats-index">
    <h1>Список квартир</h1>
    <router-link to="/flats/add" class="btn btn-primary">Добавить квартиру</router-link>

    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>Название</th>
          <th>Описание</th>
          <th>Цена</th>
          <th>Площадь (м²)</th>
          <th>Комнаты</th>
          <th>Этаж</th>
          <th>Всего этажей</th>
          <th>Адрес</th>
          <th>Доступна</th>
          <th>Изображение</th>
          <th>Действия</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="flat in flats" :key="flat.id">
          <td>{{ flat.id }}</td>
          <td>{{ flat.title }}</td>
          <td>{{ flat.description }}</td>
          <td>{{ flat.price }} ₽</td>
          <td>{{ flat.area }}</td>
          <td>{{ flat.rooms }}</td>
          <td>{{ flat.floor }}</td>
          <td>{{ flat.total_floors }}</td>
          <td>{{ flat.address }}</td>
          <td>
            <span :class="['availability', flat.is_available ? 'available' : 'unavailable']">
              {{ flat.is_available ? 'Доступна' : 'Недоступна' }}
            </span>
          </td>
          <td>
            <img
              v-if="flat.image_url"
              :src="getAssetImage(flat.image_url)"
              :alt="flat.title"
              style="width: 100px; height: auto;"
            />
            <span v-else>Нет изображения</span>
          </td>
          <td>
            <router-link :to="{ name: 'FlatsView', params: { id: flat.id } }" class="btn btn-info">
              Просмотр
            </router-link>
            <router-link :to="{ name: 'FlatsEdit', params: { id: flat.id } }" class="btn btn-warning">
              Редактировать
            </router-link>
            <button @click="deleteFlat(flat.id)" class="btn btn-danger">Удалить</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Пагинация -->
    <div class="pagination">
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
        class="btn btn-secondary"
      >
        Предыдущая
      </button>
      <span>Страница {{ currentPage }} из {{ totalPages }}</span>
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="btn btn-secondary"
      >
        Следующая
      </button>
    </div>
  </div>
</template>

<script>
import { flatsApi } from '@/services/api';

export default {
  data() {
    return {
      flats: [], // Список квартир
      currentPage: 1, // Текущая страница
      limit: 10, // Количество элементов на странице
      total: 0, // Общее количество квартир
      totalPages: 0, // Общее количество страниц
    };
  },
  created() {
    this.fetchFlats(); // Получаем список квартир при создании компонента
  },
  methods: {
    fetchFlats() {
      flatsApi.getFlats(this.currentPage, this.limit)
        .then(response => {
          console.log('Response from server:', response); // Логируем ответ сервера
          if (!response.data || !response.data.meta) {
            throw new Error('Invalid response format from server');
          }
          this.flats = response.data.data;
          this.total = response.data.meta.total;
          this.totalPages = response.data.meta.totalPages;
        })
        .catch(error => {
          console.error('Ошибка при получении списка квартир:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    changePage(page) {
      if (page < 1 || page > this.totalPages) return;
      this.currentPage = page;
      this.fetchFlats(); // Обновляем данные при смене страницы
    },
    deleteFlat(id) {
      if (!confirm('Вы уверены, что хотите удалить эту квартиру?')) {
        return;
      }

      flatsApi.deleteFlat(id)
        .then(() => {
          this.flats = this.flats.filter(flat => flat.id !== id);
          alert('Квартира успешно удалена!');
        })
        .catch(error => {
          console.error('Ошибка при удалении квартиры:', error);
          alert(`Произошла ошибка при удалении квартиры: ${error.message}`);
        });
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
  },
};
</script>

<style scoped>
.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}
.table img {
  width: 50px;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}
.btn {
  margin-right: 5px;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 14px;
}
.pagination span {
  margin: 0 10px;
  font-size: 16px;
}

/* Стили для поля "Доступность" */
.availability {
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 5px;
}
.available {
  color: #fff;
  background-color: green;
}
.unavailable {
  color: #fff;
  background-color: red;
}
</style>