<template>
  <div class="contacts">
    <!-- Шапка -->
    <header class="bg-success text-white text-center py-5">
      <h1>Контакты</h1>
      <p class="lead">Свяжитесь с нами для получения дополнительной информации.</p>
    </header>

    <!-- Секция с контактами сотрудников -->
    <section class="container my-5">
      <h2 class="text-center mb-4">Наша команда</h2>
      <div class="row">
        <div class="col-md-4 mb-4" v-for="(contact, index) in shuffledContacts" :key="index">
          <img :src="getContactImagePath(index + 1)" alt="Фото сотрудника" class="img-fluid rounded shadow-sm mb-2">
          <h4 class="mb-2">{{ contact.name }}</h4>
          <p><strong>Должность:</strong> {{ contact.position }}</p>
          <p><strong>Email:</strong> {{ contact.email }}</p>
          <p><strong>Телефон:</strong> {{ contact.phone }}</p>
        </div>
      </div>
    </section>

    <!-- Секция общих контактов -->
    <section class="container my-5 text-content">
      <h2 class="text-center mb-4">Наши контакты</h2>
      <div class="row">
        <div class="col-md-6 offset-md-3">
          <p>Мы всегда рады помочь вам в выборе идеального жилья. Если у вас есть вопросы, свяжитесь с нами по телефону или через форму обратной связи.</p>
          <p><strong>Адрес:</strong> г. Москва, ул. Недвижимости, д. 45</p>
          <p><strong>Телефон:</strong> +7 (999) 888-77-66</p>
          <p><strong>Email:</strong> info@kvartal.ru</p>
          <p><strong>Часы работы:</strong> Пн-Пт: 9:00 - 19:00, Сб: 10:00 - 16:00</p>
          <p><strong>Социальные сети:</strong> @kvartal_agency</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'ContactsView',
  data() {
    return {
      // Массив с данными для контактов
      contacts: [
        {
          name: 'Иванова Ольга Сергеевна',
          position: 'Риелтор по новостройкам',
          email: 'ivanova@kvartal.ru',
          phone: '+7 (999) 111-22-33',
        },
        {
          name: 'Петров Андрей Викторович',
          position: 'Специалист по вторичному жилью',
          email: 'petrov@kvartal.ru',
          phone: '+7 (999) 222-33-44',
        },
        {
          name: 'Смирнова Екатерина Игоревна',
          position: 'Юрист по недвижимости',
          email: 'smirnova@kvartal.ru',
          phone: '+7 (999) 333-44-55',
        },
        {
          name: 'Кузнецов Максим Александрович',
          position: 'Агент по продажам',
          email: 'kuznetsov@kvartal.ru',
          phone: '+7 (999) 444-55-66',
        },
        {
          name: 'Волкова Татьяна Владимировна',
          position: 'Консультант по ипотеке',
          email: 'volkova@kvartal.ru',
          phone: '+7 (999) 555-66-77',
        },
        {
          name: 'Соколов Дмитрий Николаевич',
          position: 'Руководитель отдела продаж',
          email: 'sokolov@kvartal.ru',
          phone: '+7 (999) 666-77-88',
        },
      ],
    };
  },
  computed: {
    // Перемешанный массив контактов
    shuffledContacts() {
      return this.shuffleArray([...this.contacts]);
    },
  },
  methods: {
    // Метод для перемешивания массива
    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array;
    },
    // Метод для получения пути к изображению контакта
    getContactImagePath(index) {
      try {
        return require(`@/assets/contact${index}.jpg`);
      } catch (error) {
        try {
          return require(`@/assets/contact${index}.jpeg`);
        } catch (e) {
          return '/images/default.jpg'; // Заглушка, если изображение отсутствует
        }
      }
    },
  },
};
</script>

<style scoped>
.contacts {
  font-family: Arial, sans-serif;
}

