<template>
  <div class="agent-view">
    <h1>Просмотр сотрудника</h1>

    <div v-if="loading">Загрузка данных...</div>
    <div v-else-if="error">{{ error }}</div>
    <div v-else>
      <!-- Фото -->
      <div class="agent-photo" v-if="agent.photo_url">
        <img :src="getAssetImage(agent.photo_url)" :alt="agent.name" />
      </div>

      <!-- Информация о сотруднике -->
      <div class="agent-info">
        <p><strong>ID:</strong> {{ agent.id }}</p>
        <p><strong>Имя:</strong> {{ agent.name }}</p>
        <p><strong>Фамилия:</strong> {{ agent.surname }}</p>
        <p><strong>Email:</strong> {{ agent.email }}</p>
        <p><strong>Телефон:</strong> {{ agent.phone || 'Не указан' }}</p>
        <p><strong>Должность:</strong> {{ agent.position }}</p>
        <p><strong>Годы опыта:</strong> {{ agent.experience_years || 0 }}</p>
        <p><strong>Дата регистрации:</strong> {{ formatDate(agent.created_at) }}</p>
        <p><strong>Доступен:</strong> {{ agent.is_available ? 'Да' : 'Нет' }}</p>

        <p><strong>Биография:</strong></p>
        <p class="bio">{{ agent.bio || 'Не указана' }}</p>
      </div>
    </div>

    <router-link to="/agents" class="btn-back">Назад к списку</router-link>
  </div>
</template>

<script>
import { agentsApi } from '@/services/api';

export default {
  data() {
    return {
      agent: null,
      loading: true,
      error: null,
    };
  },
  async created() {
    await this.fetchAgent();
  },
  methods: {
    async fetchAgent() {
      try {
        const response = await agentsApi.getAgentById(this.$route.params.id);
        this.agent = response.data;
        this.loading = false;
      } catch (error) {
        this.error = 'Ошибка при загрузке данных о сотруднике';
        this.loading = false;
        console.error('Ошибка при загрузке сотрудника:', error);
      }
    },
    formatDate(dateString) {
      if (!dateString) return 'Не указана';
      const date = new Date(dateString);
      return date.toLocaleDateString('ru-RU');
    },
    getAssetImage(imageName) {
      try {
        // Путь до папки assets
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/images/default-avatar.png'; // Заглушка (можно положить в /public/images/)
      }
    },
  },
};
</script>

<style scoped>
.agent-view {
  max-width: 700px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.agent-photo {
  text-align: center;
  margin-bottom: 20px;
}

.agent-photo img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid #ccc;
}

.agent-info p {
  margin: 8px 0;
}

.bio {
  white-space: pre-wrap;
  font-style: italic;
  color: #333;
}

.btn-back {
  display: inline-block;
  margin-top: 20px;
  text-decoration: none;
  color: #007bff;
}
.btn-back:hover {
  text-decoration: underline;
}
</style>