import { createRouter, createWebHistory } from 'vue-router';

// Главная страница и другие общие компоненты
import HomeView from '../views/HomeView.vue';
import AboutView from '../views/AboutView.vue';
import ContactsView from '../views/ContactsView.vue';


// Компоненты для квартир (flats)
import FlatsList from '../views/FlatsList.vue';
import FlatsAdd from '../views/FlatsAdd.vue';
import FlatsEdit from '../views/FlatsEdit.vue';
import FlatsView from '../views/FlatsView.vue';

// Компоненты для сотрудников (agents)
import AgentsList from '../views/AgentsList.vue';
import AgentsAdd from '../views/AgentsAdd.vue';
import AgentsEdit from '../views/AgentsEdit.vue';
import AgentsView from '../views/AgentsView.vue';

// Компоненты для авторизации
import UserRegister from '../components/UserRegister.vue';
import UserLogin from '../components/UserLogin.vue';
import AdminDashboard from '../components/AdminDashboard.vue';
import UnauthorizedView from '../views/UnauthorizedView.vue';

const routes = [
  { path: '/', name: 'Home', component: HomeView },
  { path: '/about', name: 'About', component: AboutView },
  { path: '/contacts', name: 'Contacts', component: ContactsView },


  // Маршруты для квартир
  {
    path: '/flats',
    name: 'FlatsIndex',
    component: FlatsList,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/flats/add',
    name: 'FlatsCreate',
    component: FlatsAdd,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/flats/edit/:id',
    name: 'FlatsEdit',
    component: FlatsEdit,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/flats/view/:id',
    name: 'FlatsView',
    component: FlatsView,
    meta: { requiresAuth: true, role: 'admin' }
  },

  // Маршруты для сотрудников
  {
    path: '/agents',
    name: 'AgentsIndex',
    component: AgentsList,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/agents/add',
    name: 'AgentCreate',
    component: AgentsAdd,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/agents/edit/:id',
    name: 'AgentEdit',
    component: AgentsEdit,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/agents/view/:id',
    name: 'AgentView',
    component: AgentsView,
    meta: { requiresAuth: true, role: 'admin' }
  },

  // Авторизация
  { path: '/login', name: 'Login', component: UserLogin },
  { path: '/register', name: 'Register', component: UserRegister },
  { path: '/dashboard', name: 'Dashboard', component: AdminDashboard },
  {
    path: '/unauthorized',
    name: 'Unauthorized',
    component: UnauthorizedView
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

// Навигационный хук для защиты маршрутов
router.beforeEach(async (to, from, next) => {
  if (to.meta.requiresAuth) {
    try {
      const response = await fetch('/protected');
      if (!response.ok) {
        return next('/login'); // Перенаправляем на страницу входа
      }

      const data = await response.json();
      const userRole = data.role;

      if (to.meta.role && userRole !== to.meta.role) {
        return next('/unauthorized'); // Перенаправляем на страницу "Нет доступа"
      }

      next(); // Разрешаем доступ
    } catch (error) {
      console.error('Ошибка при проверке авторизации:', error);
      next('/login'); // Перенаправляем на страницу входа
    }
  } else {
    next(); // Разрешаем доступ к другим маршрутам
  }
});

export default router;