<template>
  <div class="flat-view">
    <h1>Просмотр квартиры</h1>

    <div v-if="loading">Загрузка данных...</div>
    <div v-else-if="error">{{ error }}</div>
    <div v-else>
      <h2>{{ flat.title }}</h2>

      <p><strong>ID:</strong> {{ flat.id }}</p>
      <p><strong>Описание:</strong> {{ flat.description }}</p>
      <p><strong>Цена:</strong> {{ flat.price }} ₽</p>
      <p><strong>Площадь:</strong> {{ flat.area }} м²</p>
      <p><strong>Комнаты:</strong> {{ flat.rooms }}</p>
      <p><strong>Этаж:</strong> {{ flat.floor }}</p>
      <p><strong>Всего этажей:</strong> {{ flat.total_floors }}</p>
      <p><strong>Адрес:</strong> {{ flat.address }}</p>
      <p><strong>Доступна:</strong> {{ flat.is_available ? 'Да' : 'Нет' }}</p>

      <img
        v-if="flat.image_url"
        :src="getAssetImage(flat.image_url)"
        :alt="flat.title"
        style="width: 200px; height: auto;"
      />
      <span v-else>Нет изображения</span>
    </div>

    <router-link to="/flats" class="btn btn-secondary">Назад к списку</router-link>
  </div>
</template>

<script>
import { flatsApi } from '@/services/api';

export default {
  data() {
    return {
      flat: null,
      loading: true,
      error: null,
    };
  },
  async created() {
    const id = this.$route.params.id;

    // Проверка ID
    if (!id || isNaN(Number(id))) {
      this.error = 'Некорректный ID квартиры';
      this.loading = false;
      return;
    }

    try {
      const response = await flatsApi.getFlatById(id);
      this.flat = response.data;
    } catch (err) {
      console.error('Ошибка при загрузке квартиры:', err.message);
      this.error = 'Не удалось загрузить данные о квартире.';
    } finally {
      this.loading = false;
    }
  },
  methods: {
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
  },
};
</script>