import axios from 'axios';

const apiClient = axios.create({
  baseURL: 'https://one23123-rldu.onrender.com/api', // Убедитесь, что порт совпадает
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json',
  },
   withCredentials: true, // Важно для сессий
});

// Методы для работы с таблицей flats (квартиры)
export const flatsApi = {
  getFlats(page = 1, limit = 10) {
    return apiClient.get('/flats', {
      params: { page, limit },
    });
  },
  getFlatById(id) {
    return apiClient.get(`/flats/${id}`);
  },
  addFlat(data) {
    return apiClient.post('/flats', data);
  },
  updateFlat(id, data) {
    return apiClient.put(`/flats/${id}`, data);
  },
  deleteFlat(id) {
    return apiClient.delete(`/flats/${id}`);
  },
};

