<template>
  <div class="flat-edit">
    <h1>Редактировать квартиру</h1>
    <form @submit.prevent="updateFlat">
      <label for="title">Название:</label>
      <input type="text" id="title" v-model="formData.title" required />

      <label for="description">Описание:</label>
      <textarea id="description" v-model="formData.description"></textarea>

      <label for="price">Цена (₽):</label>
      <input type="number" id="price" v-model.number="formData.price" required />

      <label for="area">Площадь (м²):</label>
      <input type="number" id="area" v-model.number="formData.area" step="0.1" required />

      <label for="rooms">Количество комнат:</label>
      <input type="number" id="rooms" v-model.number="formData.rooms" required />

      <label for="floor">Этаж:</label>
      <input type="number" id="floor" v-model.number="formData.floor" required />

      <label for="total_floors">Всего этажей:</label>
      <input type="number" id="total_floors" v-model.number="formData.total_floors" required />

      <label for="address">Адрес:</label>
      <input type="text" id="address" v-model="formData.address" required />

      <label for="image_url">URL изображения:</label>
      <input type="text" id="image_url" v-model="formData.image_url" />

      <label for="is_available">Доступна:</label>
      <select id="is_available" v-model="formData.is_available">
        <option :value="true">Да</option>
        <option :value="false">Нет</option>
      </select>

      <button type="submit">Сохранить</button>
      <router-link to="/flats" class="btn btn-secondary">Отмена</router-link>
    </form>
  </div>
</template>

<script>
import { flatsApi } from '@/services/api';

export default {
  data() {
    return {
      formData: {
        title: '',
        description: '',
        price: null,
        area: null,
        rooms: null,
        floor: null,
        total_floors: null,
        address: '',
        image_url: '',
        is_available: true,
      },
    };
  },
  async created() {
    await this.fetchFlat();
  },
  methods: {
    async fetchFlat() {
      try {
        const response = await flatsApi.getFlatById(this.$route.params.id);
        this.formData = response.data;
      } catch (error) {
        console.error('Ошибка при загрузке квартиры:', error);
      }
    },
    async updateFlat() {
      // Проверка обязательных полей
      if (
        !this.formData.title ||
        !this.formData.price ||
        !this.formData.area ||
        !this.formData.rooms ||
        !this.formData.floor ||
        !this.formData.total_floors ||
        !this.formData.address
      ) {
        alert('Пожалуйста, заполните все обязательные поля.');
        return;
      }

      try {
        await flatsApi.updateFlat(this.$route.params.id, this.formData);
        this.$router.push({ name: 'FlatsView', params: { id: this.$route.params.id } });
      } catch (error) {
        console.error('Ошибка при обновлении квартиры:', error);
        alert('Произошла ошибка при обновлении квартиры.');
      }
    },
  },
};
</script>

<style scoped>
.flat-edit {
  margin: 20px;
}
form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
label {
  font-weight: bold;
}
input, textarea, select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: #0056b3;
}
.btn-secondary {
  background-color: #6c757d;
  text-decoration: none;
  color: white;
  padding: 10px;
  display: inline-block;
  border-radius: 5px;
}
.btn-secondary:hover {
  background-color: #5a6268;
}
</style>