<template>
  <div class="flat-create">
    <h1>Создание новой квартиры</h1>
    <form @submit.prevent="createFlat">
      <div class="form-group">
        <label for="title">Название:</label>
        <input type="text" id="title" v-model="title" required />
      </div>

      <div class="form-group">
        <label for="description">Описание:</label>
        <textarea id="description" v-model="description"></textarea>
      </div>

      <div class="form-group">
        <label for="price">Цена (₽):</label>
        <input type="number" id="price" v-model.number="price" required />
      </div>

      <div class="form-group">
        <label for="area">Площадь (м²):</label>
        <input type="number" id="area" v-model.number="area" step="0.1" required />
      </div>

      <div class="form-group">
        <label for="rooms">Количество комнат:</label>
        <input type="number" id="rooms" v-model.number="rooms" required />
      </div>

      <div class="form-group">
        <label for="floor">Этаж:</label>
        <input type="number" id="floor" v-model.number="floor" required />
      </div>

      <div class="form-group">
        <label for="total_floors">Всего этажей:</label>
        <input type="number" id="total_floors" v-model.number="totalFloors" required />
      </div>

      <div class="form-group">
        <label for="address">Адрес:</label>
        <input type="text" id="address" v-model="address" required />
      </div>

      <div class="form-group">
        <label for="image_url">URL изображения:</label>
        <input type="text" id="image_url" v-model="imageUrl" />
      </div>

      <div class="form-group">
        <label for="is_available">Доступна:</label>
        <select id="is_available" v-model="isAvailable">
          <option :value="true">Да</option>
          <option :value="false">Нет</option>
        </select>
      </div>

      <button type="submit" class="btn btn-primary">Создать</button>
      <router-link to="/flats" class="btn btn-secondary">Отмена</router-link>
    </form>
  </div>
</template>

<script>
import { flatsApi } from '@/services/api';

export default {
  data() {
    return {
      title: '',
      description: '',
      price: null,
      area: null,
      rooms: null,
      floor: null,
      totalFloors: null,
      address: '',
      imageUrl: '',
      isAvailable: true,
    };
  },
  methods: {
    createFlat() {
      const newFlat = {
        title: this.title.trim(),
        description: this.description.trim(),
        price: parseFloat(this.price),
        area: parseFloat(this.area),
        rooms: parseInt(this.rooms, 10),
        floor: parseInt(this.floor, 10),
        total_floors: parseInt(this.totalFloors, 10),
        address: this.address.trim(),
        image_url: this.imageUrl.trim(),
        is_available: this.isAvailable,
      };

      // Проверка обязательных полей
      if (!newFlat.title || !newFlat.price || !newFlat.area || !newFlat.rooms || !newFlat.floor || !newFlat.total_floors || !newFlat.address) {
        alert('Пожалуйста, заполните все обязательные поля.');
        return;
      }

      // Используем flatsApi.addFlat
      flatsApi.addFlat(newFlat)
        .then(response => {
          const flatId = response.data.id;
          alert('Квартира успешно создана!');
          this.$router.push({ name: 'FlatsView', params: { id: flatId } });
        })
        
    },
  },
};
</script>

<style scoped>
.flat-create {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input,
textarea,
select {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

.btn {
  padding: 10px 15px;
  margin-right: 10px;
}
</style>